import { Typography, Button } from '@material-ui/core';
import React from 'react';
import StepperDialogButton from './StepperDialogButton';
import * as ForcepserConfig from './ForcepserConfig';

import LinkIcon from '@material-ui/icons/Link';
import ExtensionIcon from '@material-ui/icons/Extension';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import GetAppIcon from '@material-ui/icons/GetApp';
import SettingsIcon from '@material-ui/icons/Settings';

import ConfigImage from './images/coefont_ext_config.png';
import OptionImage from './images/coefont_ext_options.png';
import DLImage from './images/coefont_ext_dl.png';

const getSteps = (title: string, tmpPath: string): [string, React.ReactNode][] => [
  [
    `Google Chrome に拡張機能をインストールする`,
    <>
      <Typography>
        現在の CoeFont Studio の仕様ではかんしくんとは直接連携できません。<br />
        この問題を解決するため、かんしくんの開発者が Chrome 拡張機能を公開しています。<br />
        <br />
        <Button variant="contained" color="primary" href="https://github.com/oov/coefont_autotxtdl" target="_blank" startIcon={<LinkIcon />}>
          CoeFont Studio Auto Text Downloader
        </Button><br />
        <br />
        この拡張機能を Google Chrome にインストールしてください。<br />
        ※この拡張機能は自動でバージョンアップされないので、必要に応じて適宜再インストールしてください。
      </Typography>
    </>,
  ],
  [
    'CoeFont Studio Auto Text Downloader のオプション設定を行う',
    <>
      <Typography>
      <ol>
        <li>Chrome の右上にある <ExtensionIcon /> をクリック</li>
        <li>CoeFont Studio Auto Text Downloader の横にある <MoreVertIcon /> をクリック</li>
        <li>表示されたメニューから [ オプション ] を選択</li>
      </ol>
      </Typography>
      <img src={OptionImage} alt="設定例" width="400" height="245" />
      <Typography>
        拡張機能がインストールされ、適切にメニューから選ぶことができればこのような入力欄が表示されます。<br />
        以下のように操作してください。
      </Typography>
      <Typography>
      <ol>
        <li>保存フォルダーに「coefont」と入力</li>
        <li>「保存」ボタンを押す</li>
        <li>残ったブラウザーのタブを閉じる</li>
        <li>開いている CoeFont Studio のタブがあれば、それも閉じる</li>
      </ol>
      </Typography>
    </>,
  ],
  [
    'CoeFont Studio 側の設定をする',
    <>
      <img src={ConfigImage} alt="設定例" width="337" height="393" />
      <Typography>
      <ol>
        <li>CoeFont Studio のセリフ入力画面を開く</li>
        <li>右上の <SettingsIcon /> 設定 を押す</li>
        <li>表示されたメニューから wav を選択</li>
      </ol>
      </Typography>
    </>
  ],
  [
    'セリフのダウンロードをテストする',
    <>
      <img src={DLImage} alt="ダウンロードボタンの場所" width="498" height="79" />
      <Typography>
      <ol>
        <li>適当にセリフを入力する</li>
        <li>セリフの右側にある <GetAppIcon /> を押してセリフをダウンロードする</li>
      </ol>
      </Typography>
      <Typography>
        保存されたファイル名が <Typography style={{ fontFamily: "monospace" }} display="inline">123455678_キャラ名_セリフ10文字.wav</Typography> の形式になれば正常な動作です。<br />
        この際に、セリフのテキストファイルも同時にダウンロードされるようになります。
      </Typography>
      </>
  ],
  [
    '監視フォルダーを設定する',
    <>
      <Typography>
      この設定ガイドを閉じて、「監視フォルダー」を設定してください。<br />
      前のステップで音声ファイルが作成されたフォルダーを、監視フォルダーに設定します。<br />
      <br />
        （パソコンのユーザー名が XXXX なら、大抵は <Typography style={{ fontFamily: "monospace" }} display="inline">C:\Users\XXXX\Downloads\coefont</Typography> です）      </Typography>
    </>,
  ],
  [
    '上手く動かないときは',
    <>
      <Typography>
        全ての設定を正しく行えたにも関わらず上手く動かないときは、CoeFont Studio 側の変更により拡張機能が動作しなくなっている可能性があります。<br />
        この場合、開発者が対応作業を行うまでは直りません。<br />
        <br />
        CoeFont Studio は現状ユーザー数が少ないため、直るのを待っているだけだと解決しづらいです。<br />
        積極的に不具合報告を行うか、別の音声合成ソフトウェアを検討してください。
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonVoiceroidUtil(props: {
  title: string;
}) {
  const { title } = props;
  const [tmpPath, setTmpPath] = React.useState("");
  const steps = React.useMemo(() => getSteps(title, tmpPath), [title, tmpPath]);
  React.useEffect(() => {
    if (!ForcepserConfig.hasAPI()) {
      return;
    }
    ForcepserConfig.getTmpPath().then(setTmpPath);
  }, []);
  return (
    <StepperDialogButton caption={`${title} の設定`} title={`${title} の設定`} steps={steps} />
  );
}
