import { Typography } from '@material-ui/core';
import React from 'react';
import StepperDialogButton from './StepperDialogButton';
import * as ForcepserConfig from './ForcepserConfig';

import SettingsIcon from '@material-ui/icons/Settings';
import HomeIcon from '@material-ui/icons/Home';
import GetAppIcon from '@material-ui/icons/GetApp';

import MenuImage from './images/coeiroink_menu.png';
import OptionsImage from './images/coeiroink_options.png';
import SaveImage from './images/coeiroink_save.png';

const getSteps = (title: string, tmpPath: string): [string, React.ReactNode][] => [
  [
    `${title} を起動する`,
    <>
      <Typography>
        {title} を起動します。
      </Typography>
    </>,
  ],
  [
    `オプションを開く`,
    <>
      <img src={MenuImage} alt="メニュー選択例" width="306" height="181" />
      <Typography>
        上部にある <SettingsIcon /> からオプションを選択します。
      </Typography>
    </>,
  ],
  [
    'オプションの設定を行う',
    <>
      <img src={OptionsImage} alt="設定例" width="417" height="313" />
      <Typography>
        オプション内の項目を以下のように設定します。
      </Typography>
      <ol>
      <li>「書き出しフォルダ固定」を有効にする</li>
      <li>「書き出しフォルダを指定」を押して、
        {
            tmpPath ?
              <>
                <Typography style={{ fontFamily: "monospace" }} display="inline">{tmpPath}</Typography> を選択する
              </>
              :
              <>
                forcepser.exe（かんしくんのメインプログラム）と同じフォルダー内にある「tmp」フォルダーを選択する
                <Typography variant="body2">
                  ※もし「tmp」フォルダーが見つからない場合は、手動で作成してください。
                </Typography>
              </>
          }

      </li>
      <li>「txtファイルを音声保存と同時に書き出し」を有効にする</li>
      <li>「txtファイルの文字コード」を「UTF-8」に設定する</li>
      </ol>
    </>,
  ],
  [
    'オプションを閉じる',
    <>
      <Typography>
        左上にある <HomeIcon /> を押して、オプションを閉じます。
      </Typography>
    </>,
  ],
  [
    '[重要] 音声の保存方法について',
    <>
      <img src={SaveImage} alt="保存例" width="123" height="223" />
      <Typography>
        作成した音声を保存する際は、左下にある <GetAppIcon /> を使用します。
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonVOICEVOX(props: {
  title: string;
}) {
  const { title } = props;
  const [tmpPath, setTmpPath] = React.useState("");
  const steps = React.useMemo(() => getSteps(title, tmpPath), [title, tmpPath]);
  React.useEffect(() => {
    if (!ForcepserConfig.hasAPI()) {
      return;
    }
    ForcepserConfig.getTmpPath().then(setTmpPath);
  }, []);
  return (
    <StepperDialogButton caption={`${title} の設定`} title={`${title} の設定`} steps={steps} />
  );
}
