import { Typography } from '@material-ui/core';
import React from 'react';
import MenuImage from './images/vv_menu.png';
import OptionsImage from './images/vv_options.png';
import SaveImage from './images/vv_save.png';
import StepperDialogButton from './StepperDialogButton';
import * as ForcepserConfig from './ForcepserConfig';

const getSteps = (title: string, tmpPath: string): [string, React.ReactNode][] => [
  [
    `${title} を起動する`,
    <>
      <Typography>
        {title} を起動します。
      </Typography>
    </>,
  ],
  [
    `オプションを開く`,
    <>
      <img src={MenuImage} alt="メニュー選択例" width="229" height="226" />
      <Typography>
        設定メニューからオプションを選択します。
      </Typography>
    </>,
  ],
  [
    'オプションの設定を行う',
    <>
      <img src={OptionsImage} alt="設定例" width="505" height="307" />
      <Typography>
        オプション内の「保存」の項目を以下のように設定します。
      </Typography>
      <ol>
      <li>「文字コード」を「UTF-8」にする</li>
      <li>「書き出し先を固定」を有効にし、
        {
            tmpPath ?
              <>
                フォルダーを <Typography style={{ fontFamily: "monospace" }} display="inline">{tmpPath}</Typography> に設定する
              </>
              :
              <>
                フォルダーを forcepser.exe（かんしくんのメインプログラム）と同じフォルダー内にある「tmp」フォルダーに設定する
                <Typography variant="body2">
                  ※もし「tmp」フォルダーが見つからない場合は、手動で作成してください。
                </Typography>
              </>
        }
      </li>
      <li>「書き出しファイル名パターン」をデフォルト設定に戻す</li>
      <li>「txtファイルを書き出し」を有効にする</li>
      </ol>
    </>,
  ],
  [
    'オプションを閉じる',
    <>
      <Typography>
        右上にある「✕」を押して、オプションを閉じます。
      </Typography>
    </>,
  ],
  [
    '参考: 音声の保存方法について',
    <>
      <img src={SaveImage} alt="保存例" width="380" height="169" />
      <Typography>
        音声を保存する際は、「ファイル」メニューにある「一つだけ書き出し」を使用します。
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonVOICEVOX(props: {
  title: string;
}) {
  const { title } = props;
  const [tmpPath, setTmpPath] = React.useState("");
  const steps = React.useMemo(() => getSteps(title, tmpPath), [title, tmpPath]);
  React.useEffect(() => {
    if (!ForcepserConfig.hasAPI()) {
      return;
    }
    ForcepserConfig.getTmpPath().then(setTmpPath);
  }, []);
  return (
    <StepperDialogButton caption={`${title} の設定`} title={`${title} の設定`} steps={steps} />
  );
}
