import { Typography } from '@material-ui/core';
import React from 'react';
import SaveImage from './images/voicepeak_save.png';
import StepperDialogButton from './StepperDialogButton';

const getSteps = (title: string): [string, React.ReactNode][] => [
  [
    '参考: セリフの保存方法について',
    <>
      <img src={SaveImage} alt="保存例" width="506" height="160" />
      <Typography>
        「Shift + Ctrl + Alt + S」を押すと、現在アクティブになっているセリフ単体を保存できます。<br />
        （裏でかんしくんを起動しているときのみ）
      </Typography>
      <ul>
      <li>注意1: 画面から入力欄などがはみ出していると、正しく動作しないことがあります。</li>
      <li>注意2: 動作テストは {title} 1.2.7 で行っています。</li>
      </ul>
    </>,
  ],
];

export default function StepperSetUpButtonCeVIO(props: {
  title: string;
}) {
  const { title } = props;
  const steps = React.useMemo(() => getSteps(title), [title]);
  return (
    <StepperDialogButton caption={`${title} の設定`} title={`${title} の設定`} steps={steps} />
  );
}
