import { Typography } from '@material-ui/core';
import React from 'react';
import MenuImage from './images/aivoice2_menu.png';
import ConfigImage from './images/aivoice2_config.png';
import Config2Image from './images/aivoice2_config2.png';
import SaveImage from './images/aivoice2_save.png';
import StepperDialogButton from './StepperDialogButton';
import * as ForcepserConfig from './ForcepserConfig';

const getSteps = (title: string, tmpPath: string): [string, React.ReactNode][] => [
  [
    `${title} を起動する`,
    <>
      <Typography>
        {title} を起動します。
      </Typography>
    </>,
  ],
  [
    '「アプリケーション設定」ダイアログを開く',
    <>
      <img src={MenuImage} alt="メインメニューの選択例" width="278" height="136" />
      <Typography>
        エディターのメインメニューから［ツール］→［アプリケーション設定］を選びます。
      </Typography>
    </>,
  ],
  [
    '「書き出し」の設定を行う',
    <>
      <img src={ConfigImage} alt="設定例" width="694" height="467" />
      <Typography>
        アプリケーション設定にある「書き出し」を上記画像の赤枠のように設定します。
      </Typography>
      <ol>
        <li>「テキストファイルを音声ファイルと一緒に保存する」を有効にする</li>
        <li>「テキストの文字コード」を「UTF-8」にする</li>
        <li>「一括書き出しの動作」を「テキストブロックごとにファイルに書き出す」にする</li>
        <li>ファイル名の指定方法を「ファイル命名規則で指定」にする</li>
        <li>「命名規則を編集」を押す</li>
      </ol>
    </>,
  ],
  [
    '「ファイル命名規則」を設定する',
    <>
      <img src={Config2Image} alt="設定例" width="754" height="136" />
      <Typography>
        「ファイル命名規則」を上記画像のように設定します。
      </Typography>
      <ol>
        <li>命名規則を <Typography style={{ fontFamily: "monospace" }} display="inline">{"{yyyyMMdd}{HHmmss}_{Character}_{Text=10}"}</Typography> に設定する</li>
      </ol>
    </>,
  ],
  [
    'ダイアログを閉じる',
    <>
      <Typography>
        「OK」ボタンを押して、「ファイル命名規則」と「アプリケーション設定」を閉じます。
      </Typography>
    </>,
  ],
  [
    '[重要] セリフの保存方法について',
    <>
      <img src={SaveImage} alt="保存例" width="363" height="167" />
      <Typography>
        「Shift + Ctrl + Alt + S」を押すと、現在アクティブになっているセリフ単体を保存できます。<br />
        （裏でかんしくんを起動しているときのみ）
      </Typography>
      <ul>
        <li>注意1: ユーザープリセットを使用している場合、ユーザープリセット名がキャラクター名として使用されます。</li>
        <li>注意2: 動作テストは かんしくん v1.6.0beta7 / {title} 2.10.1 で行っています。動かないときはバージョンをチェック！</li>
      </ul>
    </>,
  ],

];

export default function StepperSetUpButtonAITalk6(props: {
  title: string;
}) {
  const { title } = props;
  const [tmpPath, setTmpPath] = React.useState("");
  const steps = React.useMemo(() => getSteps(title, tmpPath), [title, tmpPath]);
  React.useEffect(() => {
    if (!ForcepserConfig.hasAPI()) {
      return;
    }
    ForcepserConfig.getTmpPath().then(setTmpPath);
  }, []);
  return (
    <StepperDialogButton caption={`${title} の設定`} title={`${title} の設定`} steps={steps} />
  );
}