import { Typography } from '@material-ui/core';
import React from 'react';
import MenuImage from './images/voisonatalk_menu.png';
import OptionsImage from './images/voisonatalk_options.png';
import HeaderImage from './images/voisonatalk_header.png';
import SaveImage from './images/voisonatalk_save.png';
import StepperDialogButton from './StepperDialogButton';

const getSteps = (title: string): [string, React.ReactNode][] => [
  [
    `${title} を起動する`,
    <>
      <Typography>
        {title} を起動します。
      </Typography>
    </>,
  ],
  [
    '「環境設定」ダイアログを開く',
    <>
      <img src={MenuImage} alt="メインメニューの選択例" width="305" height="129" />
      <Typography>
        エディターの右上にある ☰ アイコンを押し、［編集］→［環境設定...］を選びます。
      </Typography>
    </>,
  ],
  [
    '「ショートカット」タブを設定する',
    <>
      <img src={OptionsImage} alt="設定例" width="642" height="165" />
      <Typography>
      「ショートカット」タブを開き、上記画像の赤枠のように設定します。
      </Typography>
      <ol>
        <li>「WAVファイルをエクスポート」のショートカットキーを「F4」に設定する</li>
      </ol>
    </>,
  ],
  [
    '「環境設定」ダイアログを閉じる',
    <>
      <Typography>
        右上の「✕」ボタンを押して、「環境設定」ダイアログを閉じます。
      </Typography>
    </>,
  ],
  [
    '表示するカラムの設定をする',
    <>
      <img src={HeaderImage} alt="設定例" width="245" height="251" />
      <Typography>
      セリフリストの上部にあるヘッダーを右クリックして、上記画像の赤枠のように設定します。
      </Typography>
      <ol>
        <li>「有効」「文」「出力名」にチェックを入れる</li>
      </ol>
    </>,
  ],
  [
    '参考: セリフの保存方法について',
    <>
      <img src={SaveImage} alt="保存例" width="453" height="240" />
      <Typography>
        「Shift + Ctrl + Alt + S」を押すと、現在アクティブになっているセリフ単体を保存できます。<br />
        （裏でかんしくんを起動しているときのみ）
      </Typography>
      <ul>
      <li>注意1: {title} では、キャラクター名ではなくトラック名（赤枠で囲った部分）がファイル名に使用されます。</li>
      <li>注意2: 「トラック名」や「有効」「文」「出力名」などが画面からはみ出していると、正しく動作しません。</li>
      <li>注意3: 動作テストは {title} 1.1.5.1 で行っています。</li>
      </ul>
    </>,
  ],
];

export default function StepperSetUpButtonCeVIO(props: {
  title: string;
}) {
  const { title } = props;
  const steps = React.useMemo(() => getSteps(title), [title]);
  return (
    <StepperDialogButton caption={`${title} の設定`} title={`${title} の設定`} steps={steps} />
  );
}
