export const find = (name: string): TApplicationTemplate | undefined => {
  for (const g of templates) {
    const app = g.items.find(a => a.name === name);
    if (app) {
      return app;
    }
  }
  return undefined;
};
export const findAll = (names: string[]): TApplicationTemplate[] =>
  names.reduce<TApplicationTemplate[]>((a, name) => {
    const app = find(name);
    if (app) {
      a.push(app);
    }
    return a;
  }, []);
